<script>
export default {
  name: "BaseInput",

  inheritAttrs: false,

  props: {
    value: {
      type: [Object, String, Number],
      default: null,
    },

    label: {
      type: String,
      default: "",
    },

    placeholder: {
      type: String,
      default: "",
    },

    invalid: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    uid() {
      return this._uid;
    },

    localValue: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit("input", value);
      },
    },
  },
};
</script>

<template>
  <div class="p-1">
    <div class="flex flex-col">
      <label v-if="label" :for="uid">
        {{ label }}
      </label>

      <div
        class="flex items-center bg-white border rounded-4 hover:border-cl-blue focus:border-cl-blue transition-colors"
      >
        <div v-if="$slots['prepend']">
          <slot name="prepend" />
        </div>

        <input
          v-model="localValue"
          v-bind="$attrs"
          :id="uid"
          type="text"
          :placeholder="placeholder"
          class="w-full outline-none bg-white rounded-4 py-2 px-2.5"
        />
      </div>
    </div>
  </div>
</template>

<style scoped>
.w-full {
  width: 100%;
}

.py-2 {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

.px-2\.5 {
  padding-left: 0.625rem;
  padding-right: 0.625rem;
}

input {
  all: unset;
}

.border {
  border: 1px solid;
}

input::placeholder {
  font-size: 1rem;
}

.bg-white {
  background-color: #fff;
}

.outline-none {
  outline: none;
}

.hover\:border-cl-blue:hover {
  border-color: blue;
}

.focus\:border-cl-blue:focus {
  border-color: blue;
}

.transition-colors {
  transition-property: color, background-color, border-color;
  transition-duration: 200ms;
}

.rounded-4 {
  border-radius: 4px;
}
</style>
