<script>
import BaseInput from "@/components/base/Input.vue";

export default {
  name: "RegistrationForm",
  components: {
    BaseInput,
  },
  data() {
    return {
      fullname: "",
      surname: "",
      lastname: "",
      phoneRu: "",
      phoneSubmitRu: "",
      phoneTk: "",
      phoneSubmitTk: "",

      countryCode: {
        ru: {
          length: 10,
          initCode: 7,
          breakPointLength: 3,
          breakpoints: {
            3: true,
            6: true,
            8: true,
          },
        },
        tk: {
          length: 8,
          initCode: 993,
          breakPointLength: 3,
          breakpoints: {
            2: true,
            4: true,
            6: true,
          },
        },
      },
    };
  },
  computed: {
    ruPhoneModel: {
      get() {
        return this.phoneRu;
      },
      set(value) {
        this.phoneSubmitRu = value.replace(/\s/g, "");
        let res = "";
        let space = 0;
        for (let i = 0; i < this.phoneSubmitRu.length; i++) {
          if (this.countryCode.ru.breakpoints[res.length + 1 - space]) {
            res += this.phoneSubmitRu[i] + " ";
            space += 1;
            continue;
          }
          res += this.phoneSubmitRu[i];
        }
        this.phoneRu = res;
      },
    },
    tkPhoneModel: {
      get() {
        return this.phoneTk;
      },
      set(value) {
        this.phoneSubmitTk = value.replace(/\s/g, "");
        let res = "";
        let space = 0;
        for (let i = 0; i < this.phoneSubmitTk.length; i++) {
          if (this.countryCode.tk.breakpoints[res.length + 1 - space]) {
            res += this.phoneSubmitTk[i] + " ";
            space += 1;
            continue;
          }
          res += this.phoneSubmitTk[i];
        }
        this.phoneTk = res;
      },
    },
  },
};
</script>

<template>
  <div class="registration max-content w-full">
    <h1 class="text_cen font_cursiv">Registration</h1>
    <div class="w-full">
      <div class="border_r backg_white p_15px">
        <form class="font_cursiv bold flex_col__row10px">
          <div class="flex gap-x-2.5">
            <div class="w-1/2 px-2.5">
              <BaseInput
                v-model="fullname"
                placeholder="Вводите"
                label="Имя*"
                required
              />
            </div>
            <div class="w-1/2 px-2.5">
              <BaseInput
                v-model="surname"
                placeholder="Вводите"
                label="Фамилия*"
                required
              />
            </div>
          </div>

          <div class="flex gap-x-2.5">
            <div class="w-1/2 px-2.5">
              <BaseInput
                v-model="lastname"
                placeholder="Вводите"
                label="Отчество"
              />
            </div>
            <div class="w-1/2 px-2.5">
              <BaseInput
                v-model="ruPhoneModel"
                :minlength="
                  countryCode.ru.length + countryCode.ru.breakPointLength
                "
                :maxlength="
                  countryCode.ru.length + countryCode.ru.breakPointLength
                "
                placeholder="Вводите"
                label="Номер телефона*"
                required
              >
                <template #prepend>
                  <span class="pl-2">+{{ countryCode.ru.initCode }}</span>
                </template>
              </BaseInput>
            </div>
          </div>

          <div class="w-1/2 px-2.5">
            <BaseInput
              v-model="tkPhoneModel"
              :minlength="
                countryCode.tk.length + countryCode.tk.breakPointLength
              "
              :maxlength="
                countryCode.tk.length + countryCode.tk.breakPointLength
              "
              placeholder="Вводите"
              label="Номер телефона*"
              required
            >
              <template #prepend>
                <span class="pl-2">+{{ countryCode.tk.initCode }}</span>
              </template>
            </BaseInput>
          </div>

          <button type="submit" class="border bg-white">Отправить</button>
        </form>
      </div>
    </div>
  </div>
</template>
